import { useState } from 'react';
import './App.css';
import Model from './Model';
 function Form(){
  const [form,setform]=useState({
   name:"",
   email:"",
   password:"",
   Verification:""
  });
  const [model,setmodel]=useState(false)
  const [errormessage,seterrormessage]=useState(null)
  const inputs=form.name==""||form.email==""||form.password==""||form.Verification=="";
  function modell(e){

e.preventDefault()
seterrormessage(null)
if(form.name<=3){
seterrormessage("The name is less than 3 letters")
}
else if(form.name.length>=12){
   seterrormessage("The name is more than 12 letters")
}
else{
   seterrormessage("please enter valid name")
}
if(!email(form.email)){{
   seterrormessage("please enter valid email")
}
if(form.password.length<6){
   seterrormessage("The password is less than 6 characters")
}
if(form.password!==form.Verification){
   seterrormessage("The password and confirmation do not match")
}
setmodel(true)   


  }
  function email(email){

const email1 = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return email1.test(email);

  }

  
  function hidden(){
   if(model==true){
      setmodel(false)
  }
}

         return(
       <>   
       <div onClick={hidden}>
            <Model show={model} errormessage={errormessage}/>
   <div className="div1">
    <div className="div2">
      <form className='form' onClick={(e)=>{e.stopPropagation()}}>
         <h1 style={{  padding: "10px"}}>Registration Form</h1>
         <hr style={{width:"100%"}}></hr>
         <label>Name</label>
         <input value={form.name} type='text' onChange={(e)=>{setform({...form,name:e.target.value})}} /><br></br>
          <label>email</label>
         <input value={form.email} onChange={(e)=>{setform({...form,email:e.target.value})}} /><br></br>
         <label>password</label>   
         <div>
        <input type='password' value={form.password} onChange={(e)=>{setform({...form,password:e.target.value})}}  />
        </div><br></br>
         <label>Confirm Password</label>
         <input type='password' value={form.Verification} onChange={(e)=>{setform({...form,Verification:e.target.value})}} />
         <button  type='submit' disabled={inputs} onClick={modell} style={{backgroundColor:inputs?"gray":" rgba(240, 17, 166, 1)"}}>Submit</button>
      </form>
      </div>
    </div>
   </div>
   </>   
    );
}}

export default Form;